.intro {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-grow: 1;
  padding: 15.5rem 0 10rem;
  width: 100%;

  @media (min-width: $stop-scaling) {
    padding: 155px 0 100px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    padding: 6rem 0 5.6rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    padding: 16vh 0;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    padding: 16vh 0 5vh;
  }
}

.intro__title {
  margin: 0;
  max-width: 99rem;
  text-transform: uppercase;
  font-family: $font-alt;
  font-weight: 400;
  font-size: 12rem;
  line-height: 0.906;
  letter-spacing: -0.04em;

  .word {
    overflow: hidden;
    display: inline-block;
    vertical-align: bottom;
    white-space: nowrap;
    padding-top: 2rem;
    margin-top: -2rem;

    span {
      display: inline-block;
      transform: translate3d(0, 120%, 0);
      transition: transform 0.5s ease;
    }

    &:nth-child(1) span {
      &:nth-child(1) {
        transition-delay: 80ms;
      }
      &:nth-child(2) {
        transition-delay: 40ms;
      }
      &:nth-child(3) {
        transition-delay: 20ms;
      }
      &:nth-child(4) {
        transition-delay: 40ms;
      }
      &:nth-child(5) {
        transition-delay: 60ms;
      }
      &:nth-child(6) {
        transition-delay: 40ms;
      }
      &:nth-child(7) {
        transition-delay: 20ms;
      }
      &:nth-child(8) {
        transition-delay: 100ms;
      }
      &:nth-child(9) {
        transition-delay: 60ms;
      }
      &:nth-child(10) {
        transition-delay: 20ms;
      }
      &:nth-child(11) {
        transition-delay: 60ms;
      }
    }

    &:nth-child(2) span {
      &:nth-child(1) {
        transition-delay: 200ms;
      }
      &:nth-child(2) {
        transition-delay: 220ms;
      }
      &:nth-child(3) {
        transition-delay: 160ms;
      }
      &:nth-child(4) {
        transition-delay: 180ms;
      }
      &:nth-child(5) {
        transition-delay: 200ms;
      }
      &:nth-child(6) {
        transition-delay: 140ms;
      }
    }
  }

  &.animate--active .word span {
    transform: translate3d(0, 0, 0);
  }

  @media (min-width: $stop-scaling) {
    max-width: 990px;
    font-size: 120px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    max-width: 100%;
    font-size: 3.6rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    font-size: 10rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    font-size: 5rem;
  }
}

.intro__content {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  padding-left: 6.3rem;

  @media (min-width: $stop-scaling) {
    padding-left: 63px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    justify-content: flex-start;
    padding-left: 2.5rem;
  }
}

.intro__info {
  flex: none;
  width: 50rem;
  max-width: 50%;

  @media (min-width: $stop-scaling) {
    width: 500px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    display: none;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    display: none;
  }
}

.intro__message {
  flex: none;
  width: 50.9rem;
  max-width: 50%;
  text-transform: uppercase;
  font-size: 2.4rem;
  line-height: 1.27;
  opacity: 0;
  transform: translateY(10px);
  transition: transform 0.6s ease, opacity 0.6s ease;

  .active & {
    opacity: 1;
    transform: translateY(0);
  }

  @media (min-width: $stop-scaling) {
    width: 509px;
    font-size: 24px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    width: 100%;
    max-width: 100%;
    font-size: 1.6rem;
    line-height: 1.2;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    width: 100%;
    max-width: 100%;
    font-size: 1.6rem;
    line-height: 1.2;
  }

  p {
    margin: 0;
  }

  svg {
    @media (min-width: $stop-scaling) {
      width: 16px;
      height: 16px;
    }

    @media (max-width: $tablet) and (orientation: portrait) {
      width: 1.1rem;
      height: 1.1rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      width: 1.1rem;
      height: 1.1rem;
    }
  }
}

.intro__label {
  margin: 0 0 1.6rem;
  max-width: 25rem;
  text-transform: uppercase;
  font-size: 2.4rem;
  line-height: 1.2;

  @media (min-width: $stop-scaling) {
    margin: 0 0 16px;
    width: 250px;
    font-size: 24px;
  }
}

.intro__date {
  margin: 0 0 0 -0.5rem;
  font-family: $font-alt;
  font-weight: 400;
  font-size: 4.8rem;
  line-height: 0.906;
  letter-spacing: -0.04em;

  .word {
    overflow: hidden;
    display: inline-block;
    vertical-align: bottom;
    white-space: nowrap;
    padding-top: 2rem;
    margin-top: -2rem;

    span {
      display: inline-block;
      transform: translate3d(0, 120%, 0);
      transition: transform 0.5s ease;
    }

    &:nth-child(1) span {
      &:nth-child(1) {
        transition-delay: 560ms;
      }
      &:nth-child(2) {
        transition-delay: 540ms;
      }
    }

    &:nth-child(2) span {
      transition-delay: 560ms;
    }

    &:nth-child(3) span {
      &:nth-child(1) {
        transition-delay: 580ms;
      }
      &:nth-child(2) {
        transition-delay: 520ms;
      }
      &:nth-child(3) {
        transition-delay: 540ms;
      }
      &:nth-child(4) {
        transition-delay: 540ms;
      }
      &:nth-child(5) {
        transition-delay: 580ms;
      }
    }

    &:nth-child(4) span {
      transition-delay: 540ms;
    }

    &:nth-child(5) span {
      &:nth-child(1) {
        transition-delay: 500ms;
      }
      &:nth-child(2) {
        transition-delay: 580ms;
      }
      &:nth-child(3) {
        transition-delay: 560ms;
      }
      &:nth-child(4) {
        transition-delay: 520ms;
      }
    }
  }

  &.animate--active .word span {
    transform: translate3d(0, 0, 0);
  }

  @media (min-width: $stop-scaling) {
    margin: 0 0 0 -5px;
    font-size: 48px;
  }
}
